define('ci-on-emberjs/mirage/scenarios/default', ['exports'], function (exports) {
  exports['default'] = function (server) {
    var project = server.create('project', { id: '85c593fe-4999-49e8-854f-db81da4ad67c' });
    var commits = server.createList('commit', 20, { project_id: project.id });
    var builds = commits.map(function (commit) {
      return server.create('build', { project_id: project.id, commit_id: commit.id });
    });
    var jobs = builds.map(function (build) {
      return server.create('job', {
        project_id: project.id,
        build_id: build.id,
        commit_id: build.commit_id
      });
    });

    jobs.forEach(function (job) {
      var build = builds.findBy('id', job.build_id);

      build.job_id = job.id;
    });
  };
});